<template>
    <div class="shopcart">
        <div class="content" @click="toggleList">
          <div class="content-left">
              <div class="logo-wrapper">
                  <div class="logo" :class="{highlight: totalCount > 0}">
                      <i class="icon-shopping_cart" :class="{highlight: totalCount > 0}"></i>
                  </div>
                  <div class="num" v-show="totalCount > 0">{{totalCount}}</div>
              </div>
              <div class="price" :class="{highlight: totalPrice > 0}">{{totalPrice}}元</div>
              <div class="desc">另需配送费¥{{deliveryPrice}}元</div>
          </div>
          <div class="content-right" @click.stop.prevent="pay">
            <div class="pay" :class="payClass">
              {{payDesc}}
            </div>
          </div>
        </div>
        <div class="ball-container">
          <div class="ball" v-for="ball in balls" v-show="ball.show" transition="drop">
            <div class="inner inner-hook"></div>
          </div>
        </div>
        <div class="shopcart-list" v-show="listShow" transition="fold">
          <div class="list-header">
            <h1 class="title">购物车</h1>
            <span class="empty" @click="empty">清空</span>
          </div>
          <div class="list-content" v-el:list-content>
            <ul>
              <li class="food" v-for="food in selectedFoods">
                <span class="name">{{food.name}}</span>
                <div class="price">
                  ￥{{food.price*food.count}}
                </div>
                <div class="cartcontrol-wrapper">
                  <cartcontrol :food = "food"></cartcontrol>
                </div>
              </li>
            </ul>
          </div>
        </div>
    </div>
    <div class="list-mask" v-show="listShow" transition="fade" @click="toggleList"></div>
</template>

<script type="text/ecmascript-6">
    import cartcontrol from 'components/cartcontrol/cartcontrol'
    import BScroll from 'better-scroll'
    export default {
      props: {
        selectedFoods: {
          type: Array,
          default() {
            return []
          }
        },
        deliveryPrice: {
          type: Number,
          default: 0
        },
        minPrice: {
          type: Number,
          default: 0
        }
      },
      data() {
        return {
          balls: [
            {
              show: false
            },
            {
              show: false
            },
            {
              show: false
            },
            {
              show: false
            },
            {
              show: false
            }
          ],
          dropBalls: [],
          fold: true
        }
      },
      computed: {
        totalPrice() {
          let total = 0
          this.selectedFoods.forEach((food) => {
            total += food.price * food.count
          })
          return total
        },
        totalCount() {
          let total = 0
          this.selectedFoods.forEach((food) => {
            total += food.count
          })
          return total
        },
        payDesc() {
          if (this.totalPrice === 0) {
            return `￥${this.minPrice}元起送`
          } else if (this.totalPrice < this.minPrice) {
            let diff = 0
            diff = this.minPrice - this.totalPrice
            return `还差￥${diff}元起送`
          } else {
            return '去结算'
          }
        },
        payClass() {
          if (this.totalPrice < this.minPrice) {
            return 'not-enough'
          } else {
            return 'enough'
          }
        },
        listShow() {
          if (!this.totalCount) {
            this.fold = true
            return false
          }
          let show = !this.fold
          if (show) {
            this.$nextTick(() => {
              if (!this.scroll) {
                this.scroll = new BScroll(this.$els.listContent, {
                  click: true
                })
              } else {
                this.scroll.refresh()
              }
            })
          }
          return show
        }
      },
      methods: {
        drop(el) {
          for (let i = 0; i < this.balls.length; i++) {
            let ball = this.balls[i]
            if (!ball.show) {
              ball.show = true
              ball.el = el
              this.dropBalls.push(ball)
              return
            }
          }
        },
        toggleList() {
          if (!this.totalCount) {
            return
          }
          this.fold = !this.fold
        },
        empty() {
          this.selectedFoods.forEach((food) => {
            food.count = 0
          })
        },
        pay() {
          if (this.totalPrice < this.minPrice) {
            return
          }
          window.alert(`你需要支付${this.totalPrice}元`)
        }
      },
      transitions: {
        drop: {
          beforeEnter(el) {
            let count = this.balls.length
            while (count--) {
              let ball = this.balls[count]
              if (ball.show) {
                let rect = ball.el.getBoundingClientRect()
                let x = rect.left - 32
                let y = -(window.innerHeight - rect.top - 22)
                el.style.display = ''
                el.style.webkitTransform = `translate3d(0,${y}px,0)`
                el.style.transform = `translate3d(0,${y}px,0)`
                let inner = el.getElementsByClassName('inner-hook')[0]
                inner.style.webkitTransform = `translate3d(${x}px,0,0)`
                inner.style.transform = `translate3d(${x}px,0,0)`
              }
            }
          },
          enter(el) {
            /* eslint-disable no-unused-vars */
            let rf = el.offsetHeight
            this.$nextTick(() => {
              el.style.display = ''
              el.style.webkitTransform = 'translate3d(0,0,0)'
              el.style.transform = 'translate3d(0,0,0)'
              let inner = el.getElementsByClassName('inner-hook')[0]
              inner.style.webkitTransform = 'translate3d(0,0,0)'
              inner.style.transform = 'translate3d(0,0,0)'
            })
          },
          afterEnter(el) {
            let ball = this.dropBalls.shift()
            if (ball) {
              ball.show = false
              el.style.display = 'none'
            }
          }
        }
      },
      components: {
        cartcontrol
      }
    }
</script>

<style lang="scss" rel="stylesheet/scss">
    @import "../../common/scss/mixin.scss";
    .shopcart {
      position: fixed;
      left: 0;
      bottom: 0;
      width: 100%;
      height: 48px;
      z-index: 50;
      .content {
        display: flex;
        background: #141d27;
        color: rgba(255,255,255,.4);
        .content-left {
          flex: 1;
          font-size: 0;
          .logo-wrapper {
            display: inline-block;
            vertical-align: top;
            margin: 0 12px;
            position: relative;
            top: -10px;
            padding: 6px;
            width: 56px;
            height: 56px;
            box-sizing: border-box;
            border-radius: 50%;
            background: #141d27;
            .num {
              position: absolute;
              top: 0;
              right: 0;
              width: 24px;
              height: 16px;
              background-color: rgb(240, 20, 20);
              color: #fff;
              line-height: 16px;
              font-size: 9px;
              font-weight: 700;
              text-align: center;
              border-radius: 6px;
              box-shadow: 0 4px 8px 0 rgba(0, 0, 0, .4)
            }
            .logo {
              width: 100%;
              height: 100%;
              border-radius: 50%;
              background: #2b343c;
              text-align: center;
              &.highlight {
                background: rgb(0, 160, 220);
              }
              .icon-shopping_cart {
                font-size: 24px;
                color: #80858a;
                line-height: 44px;
                &.highlight {
                  color: #fff;
                }
              }
            }
          }
          .price {
            display: inline-block;
            vertical-align: top;
            line-height: 24px;
            border-right: 1px solid rgba(255,255,255,.1);
            padding-right: 12px;
            font-size: 16px;
            font-weight: 700;
            margin: 12px 0;
            &.highlight {
              color: #fff;
            }
          }
          .desc {
            display: inline-block;
            vertical-align: top;
            margin: 12px 0 0 12px;
            line-height: 24px;
            font-size: 10px;
          }
        }
        .content-right {
          flex: 0 0 105px;
          width: 105px;
          height: 48px;
          .pay {
            line-height: 48px;
            text-align: center;
            font-size: 12px;
            font-weight: 700;
            &.not-enough {
              background: #2b333b;
            }
            &.enough {
              background: #00b43c;
              color: #fff;
            }
          }
        }
      }
      .ball-container {
        .ball {
          position: fixed;
          bottom: 22px;
          left: 32px;
          &.drop-transition {
            transition: all 0.4s cubic-bezier(0.49,-0.29,0.75,0.41);
            .inner {
              width: 16px;
              height: 16px;
              border-radius: 50%;
              background: rgb(0,160,220);
              transition: all 0.4s linear;
            }
          }
        }
      }
      .shopcart-list {
        position: absolute;
        left: 0;
        top: 0;
        width: 100%;
        z-index: -1;
        &.fold-transition {
          transition: all .5s;
          transform: translate3d(0,-100%,0);
        }
        &.fold-enter,&.fold-leave {
          transform: translate3d(0,0,0);
        }
        .list-header {
          width: 100%;
          background: #f3f5f7;
          height: 40px;
          padding: 0 18px;
          box-sizing: border-box;
          border-bottom: 1px solid rgba(7,17,27,0.1);
          .title {
            font-size: 14px;
            color: rgb(7,17,27);
            line-height: 40px;
            font-weight: 200;
            float: left;
          }
          .empty {
            font-size: 12px;
            color: rgb(0,160,220);
            line-height: 40px;
            float: right;
          }
        }
        .list-content {
          padding: 0 18px;
          box-sizing: border-box;
          background: #fff;
          max-height: 212px;
          overflow: hidden;
          .food {
            position: relative;
            @include border-1px(rgba(7,17,27,.1));
            height: 48px;
            box-sizing: border-box;
            padding: 12px 0;
            .name {
              font-size: 14px;
              line-height: 24px;
              color: rgb(7,17,27);
            }
            .price {
              font-size: 14px;
              font-weight: 700;
              line-height: 24px;
              color: rgb(240,20,20);
              position: absolute;
              right: 90px;
              bottom: 12px;
            }
            .cartcontrol-wrapper {
              position: absolute;
              right: 0;
              bottom: 6px;
            }
          }
        }
      }
    }
    .list-mask {
      width: 100%;
      height: 100%;
      position: fixed;
      left: 0;
      top: 0;
      z-index: 40;
      backdrop-filter: blur(10px);
      &.fade-transition {
        opacity: 1;
        transition: all .5s;
        background: rgba(7,17,27,.6);
      }
      &.fade-enter,&.fade-leave {
        opacity: 0;
        background: rgba(7,17,27,0);
      }
    }
</style>



